import axios from '@/libs/api.request'
import store from '@/store'

const baseUrl = 'meetings/';

export const getMeetingList = ({current_page = 0, filter = {}, with: $with}) => {
    let data = {
        current_page,
        filter,
        with: $with //with是js保留词 所以加了$
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        method: 'get',
        params: data,
    })
}

export const getUserMeetingList = ({current_page = 0, filter = {}, with: $with}) => {
    let data = {
        current_page,
        filter: Object.assign(filter, {initiate_user_id: store.state.common.user_id}),
        with: $with //with是js保留词 所以加了$
    }
    return getMeetingList(data);
}

//获取跟department关联的及所有下属department关联的meeting
export const getDepartmentMeetingList = ({current_page = 0, filter = {}, with: $with}) => {
    let data = {
        current_page,
        filter: Object.assign(filter, {department_id: store.state.common.department_id}),
        with: ['parent'].concat($with ? $with : []) //with是js保留词 所以加了$
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl + 'department',
        method: 'get',
        params: data,
    })
}

