<template>
    <div>
        <TopButtons>
            <div slot="suffix">
                <CountDown ref="countDown"
                           :timestamp="countDownTimestamp"/>
            </div>
        </TopButtons>

        <div style="height: 10px;"></div>
        <a-spin :spinning="spinning">
            <a-card>
                <div class="xwsh_nr">
                    <div class="pad-30">
                        <h1>{{paper.name}}</h1>
                        <h2>考试时间：{{paper.duration}}分钟　　考试截止时间：{{paper.finished_at_format}}</h2>

                        <Questions v-model="questions"></Questions>

                        <div>
                            <button style="width: 200px;" class="btn btn-style02" @click="submit"> 提 交</button>
                        </div>
                    </div>
                </div>
            </a-card>
        </a-spin>
        <ResultModal v-model="visible" :data="modalData"/>
    </div>
</template>

<script>
    import TopButtons from '../在线考试列表/components/TopButtons'
    import CountDown from './components/CountDown'
    import CheckArticle from '@/components/article/check/Index'
    import ResultModal from './components/ResultModal'
    import {getExamPaper} from '@/api/examPaper'
    import {getUserExamResultByPaperId, startExamResult, submitExamResult} from '@/api/examResult'
    import Questions from '@/components/exam/questions'

    export default {
        components: {
            TopButtons, CountDown, CheckArticle, ResultModal,Questions
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        data() {
            return {
                spinning: true,
                result_id: 0,
                paper: {},
                questions: [],
                countDownTimestamp: 0,
                modalData: {},
                visible: false,
            }
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            },
        },
        methods: {
            init() {
                this.spinning = true;
                getUserExamResultByPaperId(this.thisId)
                    .then(res => {
                        if(!res.data){
                            return true;
                        }
                        if(res.data.is_submitted){
                            this.$router.push({name: '在线考试查看', params: {id: res.data.id}})
                            return false;
                        }
                        return true;
                    })
                    .then(res => {
                        if (!res) {
                            throw true;
                        }
                        return Promise.all([startExamResult(this.thisId), getExamPaper(this.thisId)])
                    })
                    .then(([res1, res2]) => {
                        this.countDownTimestamp = res1.data.submitted_at;
                        this.result_id = res1.data.id;
                        this.paper = res2.data;
                        this.questions = res2.data.questions;
                        this.spinning = false;
                    })
                    .catch(res => {
                        console.log('catch',res);
                        if(res!==true){
                            history.go(-1);
                        }
                    });
            },
            submit() {
                if (!this.result_id) {
                    return false;
                }

                submitExamResult(this.result_id, this.questions, false)
                    .then(res => {
                        let isPassed = res.data.is_passed;
                        let score = res.data.score;
                        let msg = res.msg;
                        this.modalData = {
                            isPassed,
                            score,
                            msg,
                        };
                        this.visible = true;
                        this.$refs.countDown.stop()
                    });
            },
        },
        beforeRouteLeave(to, from, next) {
            next();
        },
        created() {
            this.init();
        }
    }
</script>
