<template>
    <a-button type="primary">
        <template>
            倒计时：{{show}}
        </template>
    </a-button>
</template>

<script>
    import {formatSeconds} from '@/libs/tools'

    export default {
        name: "CountDown",
        props: {
            timestamp: {
                required: true,
                time: Number,
            }
        },
        data() {
            return {
                timeIntervalId: null,
                show: null,
                isStopped:false,
            }
        },
        methods: {
            init() {
                let now = moment().valueOf() / 1000;
                let dValue = this.timestamp > now ? this.timestamp - now : 0;

                if (!dValue && this.timeIntervalId) {
                    clearInterval(this.timeIntervalId);
                } else if (dValue && this.timeIntervalId === null) {
                    this.timeIntervalId = setInterval(() => {
                        this.init();
                    }, 1000);
                }

                this.show = formatSeconds(dValue);
            },
            stop(){
                clearInterval(this.timeIntervalId);
            },
        },
        mounted() {
            this.init();
        },
        beforeDestroy() {
            clearInterval(this.timeIntervalId);
        },
        watch: {
            timestamp() {
                this.init();
            }
        }
    }
</script>

