import axios from '@/libs/api.request'
import store from '@/store'

const baseUrl = 'articles/';

export const getArticleList = ({current_page = 0, filter = {}}) => {
    let data = {
        current_page,
        filter
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        params: data,
        method: 'get'
    })
}

