export const auditArticle = (id, data) => {
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id + '/audit',
        method: 'post',
        data,//{status,reason}
        params,
    })
}
