import axios from '@/libs/api.request'
import store from '@/store'

const baseUrl = 'exam_results/';

export const getExamResultList = ({current_page = 0, filter = {}}) => {
    let data = {
        current_page,
        filter
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        method: 'get',
        params: data,
    })
}

export const getUserExamResultList = ({current_page = 0, filter = {}}) => {
    let data = {
        current_page,
        filter
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl + store.state.common.user_id + '/user_submitted',
        method: 'get',
        params: data,
    })
}

export const getExamResult = (id) => {
    let data = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'get',
        params: data,
    })
}

export const getUserExamResultByPaperId = (paper_id) => {
    let data = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + store.state.common.user_id + '/' + paper_id+'/get_user_exam_result_by_paper_id',
        method: 'get',
        params: data,
    })
}

export const startExamResult = (paper_id) => {
    const data = {
        user_id: store.state.common.user_id,
    };
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + paper_id + '/start',
        method: 'post',
        data,
        params,
    })
}

export const submitExamResult = (resultId, answers, is_auto_submit) => {
    const data = {
        data: {
            answers,
            is_auto_submit,
        }
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + resultId + '/submit',
        method: 'post',
        data,
        params,
    })
}

